import React, { useState } from "react"
import { useTranslation } from "gatsby-plugin-react-i18next"
import {
  Nav,
  NavLogo,
  MobileIcon,
  Icon,
  NavMenu,
  NavItem,
  NavLinks,
  LanguageContainer,
  LogoLink,
  LanguageLink,
} from "./NavbarElements"
import NavLogoSrc from "./../../images/logo_amperias.svg"
import IconBurgermenuSrc from "./../../images/icon_burgermenu.svg"
import IconBurgermenuCloseSrc from "./../../images/icon_burgermenu_close.svg"

// siehe -> https://www.youtube.com/watch?v=ThzUUdIVr_I

const Navbar = () => {
  const { t, i18n } = useTranslation()
  const [toggleMenu, mobileMenuOpen] = useState(false)

  const handleClick = () => mobileMenuOpen(!toggleMenu)

  const handlePathname = () => {
    if (typeof window !== "undefined") {
      const hash = window.location.hash
      const pathname = window.location.pathname
      const url =
        pathname.substring(0, 3) === "/en" ? pathname.substring(3) : pathname
      return url + hash
    }
  }
  const navElements = t("navigation:navElements", { returnObjects: true })
  return (
    <Nav>
      <LogoLink to="/#top">
        <NavLogo src={NavLogoSrc} />
      </LogoLink>
      <MobileIcon onClick={handleClick} click={toggleMenu}>
        {
          <Icon
            src={toggleMenu ? IconBurgermenuCloseSrc : IconBurgermenuSrc}
            click={toggleMenu}
          />
        }
      </MobileIcon>
      <NavMenu onClick={handleClick} click={toggleMenu}>
        {Array.isArray(navElements) &&
          navElements.map((item, index) => (
            <NavItem
              key={index}
              small={item.small}
              hide={item.hide}
              marginTop={item.marginTop}
            >
              <NavLinks to={`/${item.link}`}>{item.title}</NavLinks>
            </NavItem>
          ))}
      </NavMenu>
      <LanguageContainer>
        {/* TODO uncomment for language
        <LanguageLink to={handlePathname()} language="en">
          {i18n.language === "en" ? <b>en</b> : "en"}
        </LanguageLink>
        <span> | </span>
        <LanguageLink to={handlePathname()} language="de">
          {i18n.language === "de" ? <b>de</b> : "de"}
        </LanguageLink>
        */}
      </LanguageContainer>
    </Nav>
  )
}

export default Navbar
