import React from 'react';
import { useTranslation } from "gatsby-plugin-react-i18next";
import { FooterContainer, FooterLinks } from './FooterElements';


function Footer() {
    const { t } = useTranslation();

    return (
        <FooterContainer>
            {Array.isArray(t("navigation:footerElements", { returnObjects: true })) &&
                t("navigation:footerElements", { returnObjects: true }).map((item, index) => (
                    <FooterLinks key={index} to={`/${item.link}`}>{item.title}</FooterLinks>
                )
            )}
        </FooterContainer>
    );
}

export default Footer;
